import React, { useState, useEffect } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  List,
  ListItem,
  ListItemText,
  Typography,
  Box,
} from "@mui/material";

const CharacteristicsMenu = ({ open, item, onClose, onChange }) => {
  const [characteristics, setCharacteristics] = useState([]);
  const [points, setPoints] = useState(0);

  useEffect(() => {
    if (item && item.characteristics) {
      setCharacteristics(item.characteristics.details || []);
      setPoints(item.characteristics.points || 0);
    }
  }, [item]);

  const availableCharacteristics = {
    0: [
      {
        name: "Frgil",
        cost: 0,
        description:
          "O item est quebrando, enferrujado, muito gasto ou com pouco combustvel, bateria ou munio. O item tem sua durabilidade reduzida em -1. Um item pode ter essa caracterstica duas vezes, se tornando Muito Frgil.",
      },
      {
        name: "Improvisado",
        cost: 0,
        description:
          "Um item feito na hora, sem refinamento de criao ou com material reaproveitado. Testes com esse item precisam de 1 Sucesso(joaninha) adicional para considerar resultados de Sucesso(joaninha) e Adaptao(cervo).",
      },
    ],
    1: [
      {
        name: "gil",
        cost: 1,
        description:
          "Uma ferramenta ou arma balanceada e capaz de efeitos poderosos em mos geis. Ao fazer um teste usando esse item voc pode gastar um uso para usar Reao em vez de Potncia.",
      },
      {
        name: "Iluminador",
        cost: 1,
        description:
          "Um item usado para iluminao. Voc pode gastar um dos usos do item para fazer com que ele brilhe o suficiente para iluminar um ambiente escuro at o final da etapa.",
      },
      {
        name: "Letal",
        cost: 1,
        description:
          "Geralmente um armamento, esse item  capaz de causar ferimentos profundos. Ao usar o item para causar dano, voc pode gastar um uso do item para receber um Sucesso(joaninha) adicional em uma das faces de um dado.",
      },
      {
        name: "Discreta",
        cost: 1,
        description:
          "Um item fino ou pequeno que pode ser facilmente escondido. Testes para esconder o item recebem 1 D10 adicional.",
      },
      {
        name: "Protetivo",
        cost: 1,
        description:
          "Pensado para a proteo, esse item pode ser usado para mitigar ferimentos. Ao sofrer dano, voc pode gastar um dos usos desse item para absorver o dano como se fossem pontos de sade.",
      },
      {
        name: "Nutritivo",
        cost: 1,
        description:
          "Um alimento ou bebida que ajuda na recuperao. Durante a etapa de recuperao voc pode gastar um dos usos do item para se alimentar e recuperar um ponto de sade.",
      },
    ],
    2: [
      {
        name: "Apto",
        cost: 2,
        description:
          "Um item focado para uso de uma aptido, seja um livro, equipamento de sobrevivncia ou ferramenta. Escolha um conhecimento ou Prtica. Ao fazer um teste da aptido escolhida voc pode gastar um dos usos do item para jogar novamente um dado para cada Adaptao(cervo) que tiver no teste (antes de manter dados).",
      },
      {
        name: "Ttica",
        cost: 2,
        description:
          "Itens reforados ou com design pensado para serem ergonmicos e eficientes. Ao fazer um teste com esse item voc pode gastar um de seus usos para trocar 1 D6 por 1 D10.",
      },
      {
        name: "Consumvel",
        cost: 2,
        description:
          "Itens feitos para serem usados uma vez, seja por serem destrudos no uso ou literalmente consumidos. O item tem apenas um uso. Todos os consumveis do mesmo tipo ocupam o mesmo espao de inventrio. Permite aplicar uma caracterstica de Categoria IV por 1 ponto ou de Categoria I por 0 pontos.",
      },
      {
        name: "Durvel",
        cost: 2,
        description:
          "Itens reforados e pensados para sobreviver ao desgaste, com baterias grandes ou muita munio. No pode ser aplicado em itens de uso nico. Aumenta a durabilidade do item em +2. Um item pode ter essa caracterstica duas vezes, recebendo um total de +5 de durabilidade e se tornando Muito Durvel.",
      },
    ],
    3: [
      {
        name: "Malandro",
        cost: 3,
        description:
          "Itens com partes que dificultam a visualizao de seu uso ou facilitem acessar as brechas de um inimigo. Ao ter sucesso usando esse item contra uma criatura voc pode gastar um de seus usos para usar Adaptao(cervo) para derrub-la ou empurr-la.",
      },
      {
        name: "Espaoso",
        cost: 3,
        description:
          "Equipamentos e vestimentas pensadas para carregar muitos itens. A quantidade de espaos de inventrio aumenta em +2. Um item pode ter essa caracterstica duas vezes, fornecendo um total de +5 de espaos de inventrio e se tornando Muito Espaoso.",
      },
      {
        name: "Espinhosa",
        cost: 3,
        description:
          "Itens afiados, pontiagudos, espinhentos, pontudos ou simplesmente perigosos. Ao ter sucesso em um teste com esse item contra uma ameaa, voc pode gastar um dos usos dele para reduzir em aumentar em Presso(coruja) o custo da prxima ativao do conflito.",
      },
      {
        name: "Estabilizado",
        cost: 3,
        description:
          "Um equipamento cuidadosamente balanceado. Ao fazer um teste com o item, voc pode gastar um dos seus usos para jogar novamente quaisquer dados com Adaptao(cervo) nas faces.",
      },
    ],
    4: [
      {
        name: "Explosivo",
        cost: 4,
        description:
          "Um item pensado para ser detonado. Ao fazer um teste com esse item, voc pode gastar um uso dele para detonar a rea em torno dele, destruindo estruturas e ferindo quaisquer criaturas na rea em torno dele.",
      },
      {
        name: "Inflamvel",
        cost: 4,
        description:
          "Itens capazes de gerar fogo numa escala perigosa. Ao fazer um teste com esse item, voc pode gastar um uso dele para deixar a rea em torno dele em chamas.",
      },
      {
        name: "Mdico",
        cost: 4,
        description:
          "Medicamentos, utenslios mdicos ou kits de primeiros socorros geralmente tm essa caracterstica. Durante a etapa de recuperao voc pode gastar um dos usos do item para fazer com que um Infectado recupere todas as caixas de sade de um nvel.",
      },
    ],
  };

  const handleAddCharacteristic = (characteristic) => {
    if (points >= characteristic.cost) {
      const updatedCharacteristics = [...characteristics, characteristic];
      setCharacteristics(updatedCharacteristics);
      setPoints(points - characteristic.cost);
      onChange({
        ...item,
        characteristics: {
          details: updatedCharacteristics,
          points: points - characteristic.cost,
        },
      });
    }
  };

  const handleRemoveCharacteristic = (index) => {
    const removedCharacteristic = characteristics[index];
    const updatedCharacteristics = characteristics.filter((_, i) => i !== index);
    setCharacteristics(updatedCharacteristics);
    setPoints(points + removedCharacteristic.cost);
    onChange({
      ...item,
      characteristics: {
        details: updatedCharacteristics,
        points: points + removedCharacteristic.cost,
      },
    });
  };

  return (
    <Dialog open={open} onClose={onClose}>
      <DialogTitle>Caractersticas do Item</DialogTitle>
      <DialogContent>
        <Typography variant="h6">Pontos Disponveis: {points}</Typography>
        <List>
          {characteristics.map((characteristic, index) => (
            <ListItem key={index}>
              <ListItemText
                primary={characteristic.name}
                secondary={characteristic.description}
              />
              <Button onClick={() => handleRemoveCharacteristic(index)}>
                Remover
              </Button>
            </ListItem>
          ))}
        </List>
        <Typography variant="h6">Adicionar Caractersticas</Typography>
        {Object.keys(availableCharacteristics).map((category) => (
          <Box key={category}>
            <Typography variant="subtitle1">Categoria {category}</Typography>
            <List>
              {availableCharacteristics[category].map(
                (characteristic, index) => (
                  <ListItem key={index} sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <ListItemText
                      primary={`${characteristic.name} (Custo: ${characteristic.cost})`}
                      secondary={characteristic.description}
                    />
                    <Button
                      onClick={() => handleAddCharacteristic(characteristic)}
                      disabled={points < characteristic.cost}
                      sx={{ marginLeft: '10px' }}
                    >
                      Adicionar
                    </Button>
                  </ListItem>
                )
              )}
            </List>
          </Box>
        ))}
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose} color="primary">
          Fechar
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default CharacteristicsMenu;