import { ThemeProvider, createTheme } from "@mui/material/styles";
import React from "react";

const theme = createTheme({
  palette: {
    mode: "dark", // Modo escuro
    primary: {
      main: "#67110e", // Cor primria
    },
    secondary: {
      main: "#ffffff", // Cor secundria
    },
    background: {
      default: "#000000", // Cor de fundo padro
      paper: "#000000", // Cor de fundo dos componentes
    },
    text: {
      primary: "#ffffff", // Cor do texto primrio
      secondary: "#ffffff", // Cor do texto secundrio
    },
  },
});

export default function AppTheme({ children }) {
  return <ThemeProvider theme={theme}>{children}</ThemeProvider>;
}